import $ from 'jquery';
// import "@/libs/ztree/js/jquery.ztree.core.js"
import "ztree/js/jquery.ztree.core.min.js";
import "ztree/js/jquery.ztree.excheck.min.js";
import "ztree/css/metroStyle/metroStyle.css";
import {
  deviceattrTree,
  alarm_configure,
  tableinfo,
  alarm_configureSave,
  alarm_configureDelete
} from '../../utils/service';
import {
  DxSelection,
  DxDataGrid,
  DxColumn,
  DxFilterRow,
  DxPager,
  DxFilterPanel,
  DxPaging,
  DxHeaderFilter,
  DxScrolling
} from "devextreme-vue/data-grid";
import zhMessages from "devextreme/localization/messages/zh.json";
import {
  locale,
  loadMessages
} from "devextreme/localization";
export default {
  components: {
    DxSelection,
    DxDataGrid,
    DxColumn,
    DxFilterRow,
    DxPager,
    DxFilterPanel,
    DxPaging,
    DxHeaderFilter,
    DxScrolling
  },
  props: {},
  created() {
    loadMessages(zhMessages);
    locale(navigator.language);
  },
  data() {
    return {
      setting: {
        treeId: 'key',
        view: {
          showIcon: true, //是否显示 文件图标
          fontCss: {}, // ztree的字体颜色
        },
        data: {
          key: {
            name: 'title'
          },
        },
        callback: {
          onClick: (event, treeId, treeNode) => {
            this.zTreeOnClick(event, treeId, treeNode); //点击生成树的点击事件
          },
        }
      },
      zNodes: '',
      treenode: '',
      title: '',
      datalist: '',
      total: 0,
      pagesize: 20,
      pageindex: 1,
      updatalistinfo: false,
      deletistinfomsg: false,
      nodeinfoForm: '',
      cslist: '',
      csname: '',
      deletid: '',
      rules: {
        typename: [],
        name: [],
        parameter_name: [{
          required: true,
          message: '请选择下拉选择',
          trigger: 'change'
        }],
        operator: [{
          required: true,
          message: '请选择运算符：',
          trigger: 'change'
        }],
        parameter_value: [{
          required: true,
          message: '告警阀值：',
          trigger: 'blur'
        }],
        level_desc: [{
          required: true,
          message: '请选择下拉选择',
          trigger: 'change'
        }],
      },
      Operator: [{
        "label": ">",
      }, {
        "label": ">=",
      }, {
        "label": "<",
      }, {
        "label": "<=",
      }, {
        "label": "=",
      }, {
        "label": "!=",
      }],
      level_desc: [{
        "label": "3级一般告警",
        "value": 3
      }, {
        "label": "2级黄色告警 ",
        "value": 2
      }, {
        "label": "1级红色告警",
        "value": 1
      }]
    };
  },
  mounted() {
    this.getdeviceattrTree()
    this.getSearchDatalist()

  },
  computed: {},
  methods: {
    // ztree点击事件
    zTreeOnClick(event, treeId, treeNode) {
      this.title = treeNode.title
      this.code = treeNode.id
      console.log("object", treeNode.title);
      var formData = {
        pageindex: this.pageindex,
        pagesize: this.pagesize,
        mark: this.code
      }
      alarm_configure(formData, res => {
        this.total = res.count
        this.datalist = res.data
        if (res.data) {
          this.datalist.map((x, index) => {
            x.key = index + 1;
          });
        }
      });
    },
    // 获取ztree数据
    getdeviceattrTree() {
      var formData = {
        type: 'all'
      }
      deviceattrTree(formData, res => {
        this.treenode = res.data[0]
        this.zNodes = this.treenode
        $.fn.zTree.init($("#treeDemo"), this.setting, this.zNodes).expandAll(true);
      });
    },
    // 初始化数据
    getSearchDatalist() {
      var formData = {
        pageindex: 1,
        pagesize: this.pagesize,
        mark: "mark"
      }
      alarm_configure(formData, res => {
        this.total = res.count
        this.datalist = res.data
        if (this.datalist) {
          this.datalist.map((x, index) => {
            x.key = index + 1;
          });
        }
      });
    },
    // 页码数改变
    handleCurrentChange(e) {
      this.pageindex = e
      var formData = {
        pageindex: e,
        pagesize: this.pagesize
      }
      search(formData, res => {
        this.total = res.count
        this.datalist = res.data
        this.datalist.map((x, index) => {
          x.key = index + 1;
        });
      });
    },
    handleSizeChange(e) {
      this.pagesize = e
    },
    // 获取类型
    getcslist() {
      var formData = {
        typecode: this.code
      }
      tableinfo(formData, res => {
        this.cslist = res.data
      });
    },
    cslistchange(value) {
      console.log('e :>> ', value);
      this.csname = value
      this.csname = this.csname + ' - 告警设置'
      this.nodeinfoForm.name = this.csname
    },
    // 数据编辑时打开弹窗
    updatalist(rowInfo) {
      this.nodeinfoForm = rowInfo.data
      this.updatalistinfo = true
      this.nodeinfoForm.typename = this.title
      this.getcslist()
      console.log('this.nodeinfoForm.id :>> ', this.nodeinfoForm);
    },
    // 编辑、新增后保存信息
    handleSubmit() {
      this.updatalistinfo = false
      this.$refs.nodeinfoForm.validate(valid => {
        if (valid) {
          let formData = new FormData();
          var parameter = ''
          this.cslist.map(item => {
            if (item.desc == this.nodeinfoForm.parameter_name) {
              parameter = item.id
            }
          })
          var level = ''
          this.level_desc.map(item => {
            if (item.label == this.nodeinfoForm.level_desc) {
              level = item.value
            }
          })
          var formData = {
            typename: this.nodeinfoForm.typename,
            name: this.nodeinfoForm.name,
            parameter: parameter,
            operator: this.nodeinfoForm.operator,
            parameter_value: this.nodeinfoForm.parameter_value,
            level: level,
            id: this.nodeinfoForm.id,
            parameter_name: this.nodeinfoForm.parameter_name,
            typecode: this.code,
            mark: this.code,

          }
          //   formData.append('id', this.nodeinfoForm.id);
          alarm_configureSave(formData, res => {});
        }
        this.$nextTick(() => {
          var formData1 = {
            pageindex: this.pageindex,
            pagesize: this.pagesize,
            mark: this.code
          }
          alarm_configure(formData1, res => {
            this.total = res.count
            this.datalist = res.data
            if (res.data) {
              this.datalist.map((x, index) => {
                x.key = index + 1;
              });
            }
          });
        })
      });

    },
    handleToggleDialog() {
      this.updatalistinfo = false;
    },
    //添加时重置信息
    reset() {
      this.nodeinfoForm = {
        typename: "",
        name: "",
        parameter: "",
        operator: "",
        parameter_value: "",
        level: "",
        parameter_name: "",
        id: "",
        typecode: "",
        mark: "",
      };
    },
    //添加
    openDialog() {
      this.reset();
      this.nodeinfoForm.typename = this.title
      this.getcslist()
      this.$nextTick(() => {
        this.$refs["nodeinfoForm"].clearValidate();
      });
      this.updatalistinfo = true;
    },
    // 删除
    deletistinfo(rowInfo) {
      this.deletistinfomsg = true
      this.deletid = rowInfo.data.id
      console.log('this.deletid :>> ', rowInfo);
    },
    // 确认删除
    Submitdeletistinfo() {
      this.deletistinfomsg = false
      var formData = {
        tablename: "alarm_configure",
        id: this.deletid
      }
      alarm_configureDelete(formData, res => {});
      this.$nextTick(() => {
        var formData1 = {
          pageindex: this.pageindex,
          pagesize: this.pagesize,
          mark: this.code
        }
        alarm_configure(formData1, res => {
          this.total = res.count
          this.datalist = res.data
          if (res.data) {
            this.datalist.map((x, index) => {
              x.key = index + 1;
            });
          }
        });
      })
    }
  },
};



