<template>
  <div class="dalarm">
    <div class="ztree">
      <ul id="treeDemo" class="ztree" @select="zTreeOnClick"></ul>
    </div>
    <div>
      <div class="bg">
        <div>
          <span class="dalarmtitle"> {{ title }} </span>
          <span v-if="title">告警策略配置</span>
          <el-button class="input" v-if="title" @click="openDialog"
            >添加告警配置</el-button
          >
        </div>
        <DxDataGrid
          :data-source="datalist"
          :allow-column-resizing="true"
          :show-column-lines="false"
          :auto-expand-all="false"
          :show-row-lines="true"
          :column-auto-width="true"
          :focused-row-enabled="true"
          :auto-navigate-to-focused-row="true"
          width="calc(240px    + 50vw)"
          key-expr="key"
          class="main_table"
          style="height: calc(337px  + 42vh);width:calc(240px    + 50vw)"
        >
          <DxColumn
            :width="140"
            data-field="id"
            alignment="center"
            caption="策略名称"
          />
          <DxColumn
            :width="170"
            data-field="mark"
            alignment="center"
            caption="设备类型"
          />
          <DxColumn
            :width="220"
            data-field="para_desc"
            alignment="center"
            caption="参数名称"
          />
          <DxColumn
            :width="160"
            data-field="condition_desc"
            alignment="center"
            caption="告警条件"
          />
          <DxColumn
            :width="170"
            data-field="level_desc"
            alignment="center"
            caption="告警等级"
          />
          <DxColumn
            :width="180"
            data-field="createtime"
            alignment="center"
            caption="设置日期"
          />
          <DxColumn
            :width="150"
            caption="操作"
            alignment="center"
            cell-template="caozuo"
          />
          <template #caozuo="{ data: rowInfo }">
            <div class="caozuo_btn">
              <span
                @click="updatalist(rowInfo)"
                class="el-icon-edit caozuo_edit"
                title="修改"
              ></span>
              <span
                @click="deletistinfo(rowInfo)"
                class="el-icon-delete caozuo_edit"
                title="删除"
              ></span>
            </div>
          </template>
          <DxScrolling use-native="false" />
        </DxDataGrid>
        <el-pagination
          :page-size="pagesize"
          :pager-count="5"
          :page-sizes="[10, 20, 30, 40]"
          @size-change="handleSizeChange"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @current-change="handleCurrentChange"
        >
        </el-pagination>
      </div>
      <el-dialog
        title="添加/修改"
        :visible.sync="updatalistinfo"
        width="70%"
        :before-close="handleToggleDialog"
      >
        <div class="dialog_form main_form">
          <el-form
            ref="nodeinfoForm"
            :model="nodeinfoForm"
            :rules="rules"
            size="medium"
            label-width="180px"
          >
            <el-form-item label="设备类型" prop="typename">
              <el-input
                v-model="nodeinfoForm.typename"
                placeholder="请输入设备类型"
                readonly
                :disabled="true"
                :style="{ width: '100%' }"
                style="background-color: #fff;color: #000;"
              ></el-input>
            </el-form-item>
            <el-form-item label="策略名称：" prop="name">
              <el-input
                v-model="nodeinfoForm.name"
                placeholder="请输入策略名称"
                readonly
                :style="{ width: '100%' }"
                style="background-color: #fff;color: #000;"
              >
              </el-input>
            </el-form-item>
            <el-form-item label="*选择参数：" prop="parameter_name">
              <el-select
                v-model="nodeinfoForm.parameter_name"
                placeholder="选择参数："
                clearable
                :style="{ width: '100%' }"
                @change="cslistchange"
              >
                <el-option
                  v-for="(item, index) in cslist"
                  :key="index"
                  :label="item.desc"
                  :value="item.desc"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="*运算符：" prop="operator">
              <el-select
                v-model="nodeinfoForm.operator"
                placeholder="请选择运算符："
                clearable
                :style="{ width: '100%' }"
              >
                <el-option
                  v-for="(item, index) in Operator"
                  :key="index"
                  :label="item.label"
                  :value="item.label"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="*告警阀值：" prop="parameter_value">
              <el-input
                v-model="nodeinfoForm.parameter_value"
                placeholder="告警阀值："
                clearable
                :style="{ width: '100%' }"
              >
              </el-input>
            </el-form-item>
            <el-form-item label="*告警级别：" prop="level_desc">
              <el-select
                v-model="nodeinfoForm.level_desc"
                placeholder="请选择下拉选择"
                clearable
                :style="{ width: '100%' }"
              >
                <el-option
                  v-for="(item, index) in level_desc"
                  :key="index"
                  :label="item.label"
                  :value="item.label"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="updatalistinfo = false">取 消</el-button>
          <el-button type="primary" @click="handleSubmit('form')"
            >确 定</el-button
          >
        </span>
      </el-dialog>
      <el-dialog
        title="删除"
        :visible.sync="deletistinfomsg"
        width="30%"
        :before-close="handleToggleDialog"
      >
        <div class="dialog_form main_form">
          <span>是否删除{{ nodeinfoForm.name }}的数据记录？</span>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="deletistinfomsg = false">取 消</el-button>
          <el-button type="primary" @click="Submitdeletistinfo('form')"
            >确 定</el-button
          >
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import dalarm from "./dalarm";
export default dalarm;
</script>
<style scoped>
@import url("./dalarm.css");
@import url("../../assets/css/houtaicommon.css");
</style>



